.top {
  width: 100%;
  height: 80px;
  background-color: black;
  display: flex;
}
.top .top-btn {
  width: 33.33%;
  height: 100%;
  text-align: center;
  font-size: 20px;
  color: rgb(95, 147, 173);
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-weight: bolder;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.top .menu {
  position: fixed;
  top: 80px;
  left: -100%;
  width: 100%;
  height: 300px;
  background-color: rgba(0, 255, 255, 0.338);
  transition: 1s;
}
.top .menu.showing {
  left: 0;
}
.top.no-responsive {
  display: none;
}/*# sourceMappingURL=top.css.map */