.team-line {
    width: 90%;
    height: 210px;
    margin: 0px auto;
    position: relative;
    border: solid 2px rgba(0, 0, 0, 0.284);
    background-color: rgba(46, 46, 46, 0.613);
    .line-lane {
        width: 90%;
        margin: 5px auto -5px auto;
        height: 20px;
        color: rgb(209, 215, 215);
        display: flex;
        font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
        justify-content: space-between;
        .lane {
            width: 50px;
        }
    }
    .line {
        width: 90%;
        margin:  5px auto;
        height: 50px;
        display: flex;
        justify-content: space-between;
        .role {
            width: 50px;
            height: 50px;
            border: solid 1px black;
            overflow: hidden;
            position: relative;
            .role-lane {
                position: absolute;
                width: 100%;
                height: 20px;
                top: -20px;
                background-color: aqua;
                z-index: 1000;
            }
            &.ok {
                
                border: solid 2px rgb(66, 133, 187);
                animation: bord 2s infinite;
                @keyframes bord {
                    0% {border-color:  rgb(66, 133, 187);}
                    50% {border-color: rgb(53, 69, 73);}
                    100%{border-color:  rgb(66, 133, 187);}
                }
            }
            img {
            height: 150%;
            width: 150%;
            margin-left: -25%;
            object-fit: cover;
            }
        }
        .arrow-team {
            width: calc(100% - 250px);
            height: 100%;
            background-color: aqua;
        }
    }
    .team-index{
        position: absolute;
        bottom: 10px;
        left: 5%;
        height: 110px;
        width: 90%;
        display: flex;
        border: solid 2px black;
        background-color: rgb(59, 59, 59);
        .team-stat-title {
            width: 8%;
            height: 100%;
            .title-stat {
                width: 100%;
                height: 50%;
                display: flex;
                font-size: 14px;
                flex-direction: column;
                justify-content: center;
                border: solid 1px rgb(34, 82, 121);
                border-right-width: 2px ;
                color: rgb(142, 142, 142);
                background-color: rgba(20, 20, 20, 0.781);
                &.being {
                    border: solid 2px rgb(51, 118, 173);
                    border-right: unset; 
                    background-color: unset;
                    color: white;}
            }
        }
        .team-rate {
            width:62%;
            height: 100%;
            position: absolute;
            left: 8%;
            border: solid 2px rgb(51, 118, 173);
            border-left: unset;
            border-right: unset;
            display: flex;
            flex-direction: column;
            justify-content: center;
            display: none;
            &.showing {display: flex;}
            .chiso {
                width: 14%;
                height: 18%;
                border: solid 1px rgb(33, 75, 109);
                background-color: rgb(0, 0, 0);
                color: rgb(160, 153, 153);
                font-size: 13px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                position: absolute;
                &.cs1 {left: 3%;top: 5%;}
                &.cs2 {right: 3%;top: 5%;}
                &.cs3 {right: 3%;top: 42%;}
                &.cs4 {right:3%;bottom: 5%;}
                &.cs5 {left: 3%;bottom:5%;}
                &.cs6 {left: 3%;top: 42%;}
            }
        }    
        .team-bonus {
            width: 30%;
            height: 100%;
            position: absolute;
            right: 0;
            padding: 2px 2px;
            background-color: rgb(23, 22, 22);
            border: solid 2px rgb(51, 118, 173);
            color: white;
            font-size: 12px;
            font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
            h3{
                margin-bottom: 7px;
                color: rgb(138, 167, 246);
                font-size: 10px;
                width: 100%;
                border-bottom: solid 1px rgb(51, 118, 173);}
            div {
                display: flex;
                justify-content: space-around;
            }
            
        }
    }  
}